import "./Copy.css";
import React, { useState, useEffect } from 'react';
import axios from 'axios';



function Copy() {
  const [jsonData, setJsonData] = useState(null);

  function corp(no){
    window.open("http://www.ftc.go.kr/bizCommPop.do?wrkr_no="+no,"","width=500 height=500");
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('http://whdwns268.cafe24.com/portfolio1/admin/config_form_react.do');
        setJsonData(response.data);
        //console.log(response);
      } catch (error) {
        console.error('Error fetching JSON data:', error);
      }
    };

    fetchData();
  }, []);

  if (!jsonData) {
    return <div>Loading...</div>;
  }
  return (
    <div className="copy_full">
      <div className="copy_full_title">
        <span className="copy_inner_title">
          <ul>
            <li>회사소개</li>
            <li>이용약관</li>
            <li>개인정보취급방침</li>
            <li>이용안내</li>
            <li>광고/제휴문의</li>
          </ul>
          <ul>
            <li><img src="./mall_img/ico/customer_center.gif" /></li>
            <li>{jsonData[3]}</li>
          </ul>
        </span>
        <span className="copy_inner_index">
          <div>
            <table className="copy_table">
              <tbody>
                <tr>
                  <td>상호명:{jsonData[0]} </td>
                  <td>대표:{jsonData[1]} </td>
                  <td>사업자등록번호:{jsonData[2]} <a className="cupoint" onClick={() => corp('1188120586')}>[사업자정보확인]</a> </td>
                </tr>
                <tr>
                  <td colSpan={2}>통신판매업신고번호:{jsonData[4]} </td>
                  <td>사업자 주소:{jsonData[5]}</td>
                </tr>
                <tr>
                  <td colSpan={2}>개인정보책임자:{jsonData[6]}</td>
                  <td>개인정보 책임자 E-mail:{jsonData[7]}</td>
                </tr>
                <tr>
                <td colSpan={4}>저작권 ⓒ 도메인 주소 All Rights reserved</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div>
            <ul>
              <li><img src="./mall_img/ico/bank_info.gif" className="imgbank" /></li>
              <li>무통장 은행:{jsonData[8]}</li>
              <li>은행계좌번호:{jsonData[9]}</li>
            </ul>
          </div>
        </span>
      </div>
    </div>
  );
}

export default Copy;
