import React, { useState, useEffect } from 'react';
import useMeasure from 'react-use-measure';
import { useSpring, animated } from '@react-spring/web';
import styles from './Top.module.css';
import './Top.css';

function Top() {
  const [open, toggle] = useState(true);
  const [ref, { height }] = useMeasure();
  const [userid, setLogState] = useState(null);

  useEffect(() => {
    setLogState(sessionStorage.getItem('userid'));
    console.log('userid', userid);
  }, []);

  const props = useSpring({
    height: open ? 70 : 0,
    opacity: open ? 1 : 0,
  });

  const fontcolor_red = { color: '#E82115' };

  function maingo() {
    window.location.href = './index.html';
  }

  function coupongo() {
    window.location.href = './coupon_insert.jsp';
  }

  function saerchgo(e) {
    var frm = document.getElementById('frm');
    if(frm.seach.value==""){
        alert("검색어를 입력해주세요");
    }
    frm.submit();
  }

    return (
        <form id="frm" method="get" action="./seach.do" onSubmit={saerchgo}>
        <ul className="top_ulcss">
          <animated.li ref={ref} className={styles.top_ulcss_li1} style={props}>
            <input type="button" value="X" onClick={() => toggle(!open)} />
          </animated.li>
          <li>
            <span className="top_ul_li_list">
              <div>합리적인 쇼핑 베스트샵! 네이버에서 베스트샵을 검색하세요!</div>
              <ul className="top_ul_userselect">
                {userid === null ? (
                    <>
                    <li>
                        <a href="./login.jsp">로그인</a>
                    </li>
                    <li>
                        <a href="./agree.jsp">회원가입</a>
                    </li>
                    </>
                ) : (
                    <>
                    <li>
                        <a>{userid}님 환영합니다.</a>
                    </li>
                    <li>
                        <a href="./sessionlogout.do">[로그아웃]</a>
                    </li>
                    </>
                )}
                <li>
                  장바구니 <a style={fontcolor_red}>0</a>{' '}
                </li>
                <li style={fontcolor_red}>마이페이지 ▼</li>
                <li>고객센터 ▼</li>
              </ul>
            </span>
          </li>
          <li>
            <ul>
              <li>
                <img src="./topimage/logo.gif" onClick={maingo} />
              </li>
              <li>
                <div className="serach_div">
                  <input
                    type="text"
                    className="serach_input"
                    name="seach"
                    placeholder="검색할 상품명을 입력하세요"
                  />
                  <input type="submit" className="serach_input_btn" value="　" />
                </div>
              </li>
              <li>
                <img src="./topimage/coupon.jpg" className="coupon_size" onClick={coupongo} />
              </li>
            </ul>
          </li>
          <li>
            <div className={styles.container}></div>
          </li>
        </ul>
      </form>
    );
  };

export default Top;
