import "./Newproduct.css";
import React, { useState, useEffect } from 'react';
import axios from 'axios';

function Newproduct(){

    const [jsonData, setJsonData] = useState(null);

    useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('http://whdwns268.cafe24.com/portfolio1/admin/prolist_api.do');
        setJsonData(response.data);
        console.log(response);
      } catch (error) {
        console.error('Error fetching JSON data:', error);
      }
    };

    fetchData();
  }, []);

  if (!jsonData) {
    return <div>Loading...</div>;
  }
return(
    <div className="newprodiv">
        <p><img src="./mall_img/ico/new_arrival.gif"></img></p>
    {jsonData.slice(0, 10).map((item, index) => (
      <ul key={index}>
        <li><img src={item.pfile}></img></li>
        <li>{item.title}</li>
        <li>{item.subtitle}</li>
        <li>{item.price.toLocaleString()}원</li>
        <li>{item.discount}%</li>
      </ul>
  ))}
    </div>
);
}

export default Newproduct;
