import "./Notice.css";
import React, { useState, useEffect } from 'react';
import axios from 'axios';


function Notice(){
    const [jsonData, setJsonData] = useState(null);

    useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('http://whdwns268.cafe24.com/portfolio1/admin/notice_api.do');
        setJsonData(response.data);
        //console.log(response);
      } catch (error) {
        console.error('Error fetching JSON data:', error);
      }
    };

    fetchData();
  }, []);

  if (!jsonData) {
    return <div>Loading...</div>;
  }

return(
    <div className="notice_div">
            <span class="notice_span_tag">
            <p>NOTICE</p>
            <a>+MORE</a>
            </span>

            <span class="notice_span_noti">
            <ul>
                <li><a></a>{jsonData[1]}</li>
                <li><a></a>{jsonData[4]}</li>
                <li><a></a>{jsonData[7]}</li>
                <li><a></a>{jsonData[10]}</li>
                <li><a></a>{jsonData[13]}</li>
            </ul>
            <ul>
                <li>{jsonData[2]}</li>
                <li>{jsonData[5]}</li>
                <li>{jsonData[8]}</li>
                <li>{jsonData[11]}</li>
                <li>{jsonData[14]}</li>
            </ul>
            </span>
    </div>
);
}

export default Notice;
