import { Navigation, Pagination, Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import { useEffect, useState } from 'react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import './Banner.css';
import axios from 'axios';

const Banner = () => {
  const [swiper, setSwiper] = useState(null);
  const [jsonData, setJsonData] = useState(null);

  const handleMouseEnter = () => {
    if (swiper) {
      swiper.autoplay.stop();
    }
  };

  const handleMouseLeave = () => {
    if (swiper) {
      swiper.autoplay.start();
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get('http://whdwns268.cafe24.com/portfolio1/admin/banner_api.do');
        setJsonData(response.data);
        
      } catch (error) {
        console.error('Error fetching JSON data:', error);
      }
    };

    fetchData();
  }, []);

  if (!jsonData) {
    return <div>Loading...</div>;
  }

  return (
    <div className="banner_div" onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
      <Swiper
        modules={[Navigation, Pagination, Autoplay]}
        spaceBetween={50}
        slidesPerView={1}
        pagination={{
          clickable: true,
          renderBullet: function (index, className) {
            return '<span class="' + className + '">' + (index + 1) + '</span>';
          },
        }}
        scrollbar={{ draggable: true }}
        autoplay={{ delay: 1000, disableOnInteraction: false }}
        onSwiper={(swiper) => setSwiper(swiper)}
      >
        {jsonData[0] && (<>
          <SwiperSlide>
            <a href={jsonData[1]}>
              <img src={jsonData[0]} alt="Banner 2" />
            </a>
          </SwiperSlide>
        </>)}

        {jsonData[2] && (<>
          <SwiperSlide>
            <a href={jsonData[3]}>
              <img src={jsonData[2]} alt="Banner 2" />
            </a>
          </SwiperSlide>
        </>)}

        {jsonData[4] && (<>
          <SwiperSlide>
            <a href={jsonData[5]}>
              <img src={jsonData[4]} alt="Banner 2" />
            </a>
          </SwiperSlide>
          </>)}

        {jsonData[6] && (<>
          <SwiperSlide>
            <a href={jsonData[7]}>
              <img src={jsonData[6]} alt="Banner 2" />
            </a>
          </SwiperSlide>
        </>)}
      </Swiper>
    </div>
  );
};

export default Banner;
